body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;

  background-color: rgba(211, 211, 211, 0.8);
  z-index: 999;
}

.login-window {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  height: 30%;
  padding: 50px 0;

  background-color: white;
}

.login-window input{
  height: 30px;

  font-size: 30px;
}

.login-window button {
  width: 200px;
  height: 50px;

  font-size: 30px;
  border: none;
}

.game {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/game/static/media/backgroundImage.e5d092aa.png);
  background-size: 100%;
}

.enemy {
  position: absolute;
  top: 7%;
  left: 65%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 80%;
}

.enemy img {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}

.healthBarBorder {
  width: 500px;
  height: 25px;

  border: 1px solid green;
  font-size: 20px;
  font-weight: bold;
}

h2 {
  margin: 0;
  padding: 0;
}

.enemyModel .body {
  position: absolute;
  top: 67%;
  left: 49%;
  z-index: 2;
}

.enemyModel .head {
  position: absolute;
  top: 53%;
  left: 45%;
  z-index: 3;
  -webkit-animation: enemyHeadAnimation 2s infinite linear;
          animation: enemyHeadAnimation 2s infinite linear;
}

.enemyModel .leftArm {
  position: absolute;
  top: 68%;
  left: 42%;
  z-index: 1;
}

.enemyModel .leftLeg {
  position: absolute;
  top: 86%;
  left: 53%;
}

.enemyModel .weapon {
  position: absolute;
  top: 73%;
  left: 25%;
  z-index: 0;
}

.enemyModel .rightArm {
  position: absolute;
  top: 68%;
  left: 68%;
  z-index: 3;
}

.enemyModel .rightLeg {
  position: absolute;
  top: 87%;
  left: 62%;
}

@-webkit-keyframes enemyHeadAnimation {
  from {top: 53%;}
  25% {top: 54%;}
  50% {top: 55%;}
  75% {top: 54%;}
  to {top: 53%;}
}

@keyframes enemyHeadAnimation {
  from {top: 53%;}
  25% {top: 54%;}
  50% {top: 55%;}
  75% {top: 54%;}
  to {top: 53%;}
}

.hero {
  position: absolute;
  top: 7%;
  left: 5%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 80%;
}

.heroModel .hat {
  position: absolute;
  top: 59%;
  left: 30%;
  z-index: 4;
  -webkit-animation: hatAnimation 2s infinite linear;
          animation: hatAnimation 2s infinite linear;
}

.heroModel .head {
  position: absolute;
  top: 72%;
  left: 37%;
  z-index: 3;
  -webkit-animation: heroHeadAnimation 2s infinite linear;
          animation: heroHeadAnimation 2s infinite linear;
}

.heroModel .mantle {
  position: absolute;
  top: 75%;
  left: 32%;
  z-index: 2;
}

.heroModel .fingersLeft {
  position: absolute;
  top: 87%;
  left: 28%;
  -webkit-transform: rotate(125deg);
          transform: rotate(125deg);
  z-index: 2;
}

.heroModel .fingersRight {
  position: absolute;
  top: 82%;
  left: 68%;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  z-index: 2;
}

.heroModel .fistLeft {
  position: absolute;
  top: 86%;
  left: 27%;
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
}

.heroModel .fistRight {
  position: absolute;
  top: 81%;
  left: 64%;
}

.heroModel .legLeft {
  position: absolute;
  top: 90%;
  left: 35%;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  z-index: 2;
}

.heroModel .legRight {
  position: absolute;
  top: 89%;
  left: 55%;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  z-index: 2;
}

.heroModel .sleeveLeft {
  position: absolute;
  top: 82%;
  left: 28%;
  -webkit-transform: rotate(40deg);
          transform: rotate(40deg);
  z-index: 2;
}

.heroModel .sleeveRight {
  position: absolute;
  top: 79%;
  left: 53%;
  -webkit-transform: rotate(-50deg);
          transform: rotate(-50deg);
}

.heroModel .stick {
  position: absolute;
  top: 69%;
  left: 63%;
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
}

@-webkit-keyframes heroHeadAnimation {
  from {top: 72%;}
  25% {top: 73%;}
  50% {top: 74%;}
  75% {top: 73%;}
  to {top: 72%;}
}

@keyframes heroHeadAnimation {
  from {top: 72%;}
  25% {top: 73%;}
  50% {top: 74%;}
  75% {top: 73%;}
  to {top: 72%;}
}

@-webkit-keyframes hatAnimation {
  from {top: 59%;}
  25% {top: 60%;}
  50% {top: 61%;}
  75% {top: 60%;}
  to {top: 59%;}
}

@keyframes hatAnimation {
  from {top: 59%;}
  25% {top: 60%;}
  50% {top: 61%;}
  75% {top: 60%;}
  to {top: 59%;}
}


.choiseAction {
  position: absolute;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}




.math {
